<template lang="pug">
  .nav
    header#header.header
        .links
          ul.menu
              li.theme_switcher
                button(@click="change" class="switch")
                  img(class="theme-color" id="svg-chng" src="data:image/svg+xml;base64,")
</template>

<script>
export default {
  name: "Header",
  methods: {
  handleSCroll() {
    let header = document.querySelector(".nav");
    if (window.scrollY > 5) {
        header.className = "nav nav-scroll";
    } else {
        header.className = "nav";
    }
  },
  change() {
    let x = document.getElementById("svg-chng");
    let mainBody = document.getElementsByTagName("BODY")[0];
    if (mainBody.className == "dark") {
        x.src = "data:image/svg+xml;base64,";
    } else {
        x.src = "data:image/svg+xml;base64,";
    }
    mainBody.classList.toggle("dark");
  }
  },
  created() {
    window.addEventListener("scroll", this.handleSCroll);
  },
  destroyed() {
    window.removeEventListener("scroll", this.handleSCroll);
  }
};
</script>